import Vue from 'vue';
import VueRouter from 'vue-router'
import Dashboard from "@/views/Dashboard";
import Outgoing from "@/views/Outgoing";
import Incoming from "@/views/Incoming";
import Citizen from "@/views/Citizen";
import Corporate from "@/views/Corporate";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Dashboard',
        component: Dashboard
    },
    {
        path: '/outgoing',
        name: 'Outgoing',
        component: Outgoing
    },
    {
        path: '/incoming',
        name: 'Incoming',
        component: Incoming
    },
    {
        path: '/citizen',
        name: 'Citizen',
        component: Citizen
    },
    {
        path: '/corporate',
        name: 'Corporate',
        component: Corporate
    },
]

const router = new VueRouter({
    routes
})

export default router